.header{
  text-align-last: center;
  font-weight: 500;
  font-size: 34px;
  font-family: 'Times New Roman', Times, serif;
}

.item{
  text-decoration: none;
  color: black;
}

.item :hover {
  color: rgb(57, 173, 125);
}

.image{
  display: block;
  max-width: 100%;
  height: auto;
}

.container{
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto
}